* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #1C1C1C;
    font-family: 'Inter', sans-serif;
}

main {
    max-width: 550px;
    /* height: 868px; */
    background-color: #23252C;
    margin: auto;
    padding: 30px 0; 
}

.container {
    width: 317px;
    /* height: calc(317 + 399 + 64)px; */
    margin: 0 117px;       
    background-color: #1A1B21; 
    border-radius: 13px;
}

.info-img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.info {
    text-align: center;
    margin-block-start: 21px;
}

.info h2 {
    color: #fff;
    font-weight: 700;
    font-size: 25px;
}

.info > p {
    color: #F3BF99;;
    font-weight: 400;
    font-size: 12.8px;
    /* line-height: 15.49px; */
}

.info small {
    color: #F5F5F5;
}

.info-btn {
    width: 80%;
    border-radius:6px;
    font-size: 14px;
    background-color: #D1D5DB;
    padding: 7px 0;
    cursor: pointer;
    margin-block-start: 16px;
    margin-block-end: 25px;
}

.info-btn .fa-envelope {
    color: #1E1F26;
    font-size: 12.8px; 
    margin-inline-end: 9.6px;
}

.about {
    width: 247px;
    margin: auto;
}

.txt-heading {
    color: #F5F5F5;
    font-size: 16px;
    font-weight: 700;
    margin-block-end: 5px;
}

.txt-para {
    color: #DCDCDC;
    font-size: 10.24px;
    font-weight: 400;
    line-height: 15.36px;
}

.interests {
    width: 247px;
    margin: auto;
    margin-block-start: 20px;
}

footer {
    background-color: #161619;
    margin-block-start: 24px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding: 19.5px 0;
}

footer .fa{
    font-size: 25px;
    color: #918E9B;
}

.footer {
    margin-inline-start: 48.5px;
    margin-inline-end: 24.5px;
}

footer .fa {
    margin-inline-end: 24px;
}


